@import "../../../styles/breakpoints.scss";

.terms-of-service-page {
  @include animateEnter();
  margin-bottom: 40px;
  @include tablet {
    width: 70%;
    margin: auto;
  }

  display: flex;
  flex-direction: column;
  h2 {
    text-align: center;
  }

  section {
    display: flex;
    flex-direction: column;
    line-height: 2rem;
    h2 {
      color: #55bb9b;
      font-weight: bold;
      align-items: center;
    }
    p {
      align-items: flex-start;
    }
    h3 {
      font-weight: bold;
      align-items: flex-start;
    }
  }
}
